import { Component, Input, OnChanges, OnInit, SimpleChanges, TemplateRef } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { BsModalService } from 'ngx-bootstrap/modal';
import { Global } from '../../../../../global';
import { Common } from '../../../../utils/configuration/app/common';
import {
    Th2NovaTableModel,
    Row
} from '../../../../utils/components/th2-nova-table/th2-nova-table-config.model';
import { SDGNameDictionary } from '../../../model/sdg.enum';
import { News } from '../../api/news-api.model';
import { NewsService } from '../../news.service';

@Component({
    selector: 'news-table-component',
    templateUrl: './news-table.component.html'
})
export class NewsTableComponent implements OnChanges, OnInit {
    @Input()
    public news: News[]; // table data
    public selectedNews: News;
    public tableFieldsName = Common.newsTableFields[Global.language];
    public newsDetailFieldsName = Common.newsDetailFields[Global.language];
    public SDG: string;

    public sdgScores$ = this.newsService.sdgScores$;

    public currentPage = 1;
    public pageSize = 20;
    public numResults: number; // number of items

    public tableConfig: Th2NovaTableModel;
    private allRows = []; // row data

    private rowCount: number = -1; // row number
    private cellCount: number = 0; // cell number

    constructor(
        private modalService: BsModalService,
        private newsService: NewsService,
        private activatedRoute: ActivatedRoute
    ) {
        this.buildTableConfig();
    }

    public ngOnInit() {
        this.activatedRoute.queryParams.subscribe((param) => {
            this.tableFieldsName = Common.newsTableFields[Global.language];
            this.newsDetailFieldsName = Common.newsDetailFields[Global.language];
            this.buildTableConfig();

            if (this.news) {
                this.rowCount = -1;
                this.cellCount = 0;
                this.currentPage = 1;
                this.allRows = this.news.map(this.createRowWith.bind(this));
                this.numResults = this.allRows.length;
                this.setUpTableBody();
            }
        });
    }

    public ngOnChanges(changes: SimpleChanges): void {
        this.news = changes.news.currentValue;

        if (this.news) {
            this.rowCount = -1;
            this.cellCount = 0;
            this.currentPage = 1;
            this.allRows = this.news.map(this.createRowWith.bind(this));
            this.numResults = this.allRows.length;
            this.setUpTableBody();
        }
    }

    public buildTableConfig() {
        this.tableConfig = {
            header: {
                cells: [
                    {
                        colId: 'titleCell',
                        title: this.tableFieldsName.titleCell
                    },
                    {
                        colId: 'sourceCell',
                        title: this.tableFieldsName.sourceCell
                    },
                    {
                        colId: 'dateCell',
                        title: this.tableFieldsName.dateCell,
                        ordenable: true
                    },
                    {
                        colId: 'entityCell',
                        title: this.tableFieldsName.entityCell
                    },
                    {
                        colId: 'sdgCell',
                        title: this.tableFieldsName.sdgCell
                    }
                ]
            },
            body: []
        };
    }

    public onPageChanged(page: number) {
        this.currentPage = page;
        this.setUpTableBody();
    }

    // 将News转换成表格需要的行数据结构
    private createRowWith(news: News): Row {
        this.rowCount += 1;
        this.cellCount += 5;
        return {
            rowId: `row-${this.rowCount}`,
            cells: [
                {
                    cellId: `cell-${this.cellCount - 5}`,
                    templateRef: 'title',
                    context: {
                        title:
                            news.title.length <= 90 ? news.title : news.title.slice(0, 90) + '...'
                    }
                },
                {
                    cellId: `cell-${this.cellCount - 4}`,
                    templateRef: 'source',
                    context: {
                        source: news.source
                    }
                },
                {
                    cellId: `cell-${this.cellCount - 3}`,
                    templateRef: 'date',
                    context: {
                        date: news.date
                    }
                },
                {
                    cellId: `cell-${this.cellCount - 2}`,
                    templateRef: 'entity',
                    context: {
                        entity: news.entity
                    }
                },
                {
                    cellId: `cell-${this.cellCount - 1}`,
                    templateRef: 'sdg',
                    context: {
                        sdg: SDGNameDictionary[Global.language][news.sdgId]
                    }
                }
            ]
        };
    }

    // 更新表格的配置数据，tableConfig的body设置成当前页的数据
    private setUpTableBody() {
        this.tableConfig.body = [];
        const initIdx = (this.currentPage - 1) * this.pageSize;
        const lastIdx = this.currentPage * this.pageSize;
        this.tableConfig.body = this.allRows.slice(initIdx, lastIdx);
    }

    private clickHeader(event) {
        // console.log(event);
        const colId = event.colId;
        const asc = event.asc;
        const desc = event.desc;

        if (colId === 'dateCell') {
            if (asc === true) {
                this.news = this.news.sort((a, b) => {
                    return a.date > b.date ? 1 : a.date === b.date ? 0 : -1;
                });
            } else if (desc === true) {
                this.news = this.news.sort((a, b) => {
                    return a.date > b.date ? -1 : a.date === b.date ? 0 : 1;
                });
            }
        }

        // 重绘表格
        this.rowCount = -1;
        this.cellCount = 0;
        this.currentPage = 1;
        this.allRows = this.news.map(this.createRowWith.bind(this));
        this.setUpTableBody();
    }

    // private clickCell(event) {}

    private doubleClickCell(template: TemplateRef<any>, event) {
        // console.log(event);
        try {
            const rowId = Number(event.rowId.slice(4));
            this.selectedNews = this.news[rowId];

            // console.log(this.selectedNews);

            const parameters = {
                dateFrom: '',
                dateTo: '',
                sdg: [0],
                entity: [''],
                news_id: this.selectedNews.newsId,
                lang: this.selectedNews.lang
            };

            this.newsService.loadSDGScores(parameters);

            this.SDG = SDGNameDictionary[Global.language][this.selectedNews.sdgId];
            this.modalService.show(template, { class: 'modal-lg' });
        } catch (e) {
            console.log(e);
        }
    }
}
