import { AfterViewInit, Component, ViewChild, Output, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Thin2Logger } from 'thin2-log';
import { Th2GenericFormGroupModel } from '../../../../utils/forms/models/th2-generic-form-group.model';
import { Th2MatSelectModel } from '../../../../utils/forms/models/form/th2-mat-select.model';
import { Th2MatSelectOptionModel } from '../../../../utils/forms/models/form/th2-mat-select-option.model';
import { FORM_CONFIG_EN, FORM_CONFIG_ES } from './filter-form.config';
import { Th2GenericFormComponent } from '../../../../utils/forms/th2-generic-form.component';
import { Th2GenericFormHelper } from '../../../../utils/forms/th2-generic-form-helper';
import { EventEmitter } from '@angular/core';
import { NewsFormQuery, NewsQuery } from '../../api/news-api.model';
import { SDGNameDictionary, ESGDictionary } from '../../../model/sdg.enum';
import { EntityNameDictionary } from '../../../model/entity.enum';
import { NormalizeTime } from '../../../../services/NormalizeTime';
import { Global } from '../../../../../global';
import { Common } from '../../../../utils/configuration/app/common';

@Component({
    selector: 'filter-form-component',
    templateUrl: './filter-form.component.html'
})
export class FilterFormComponent implements AfterViewInit, OnInit {
    @Output()
    public onFormSubmit: EventEmitter<NewsQuery> = new EventEmitter();

    @ViewChild(Th2GenericFormComponent)
    public th2GenericFormComponent: Th2GenericFormComponent;

    public formConfig = Global.language === 'es' ? FORM_CONFIG_ES : FORM_CONFIG_EN;

    public currentSDGOptions = [];

    public currentEntityOptions = [];

    constructor(
        private LOGGER: Thin2Logger,
        private normalizeTime: NormalizeTime,
        private activatedRoute: ActivatedRoute
    ) {}

    public ngOnInit() {
        this.buildFormConfig();

        this.activatedRoute.queryParams.subscribe((param) => {
            this.buildFormConfig();
        });
    }

    public ngAfterViewInit() {
        this.LOGGER.debug('Initializing Form Helper at FilterFormComponent');
        Th2GenericFormHelper.create(
            this.formConfig,
            this.th2GenericFormComponent.formGroup,
            this.LOGGER
        );

        // tslint:disable-next-line:max-line-length
        this.currentSDGOptions = this.th2GenericFormComponent.formGroup.controls.filterForm.value.sdg;

        // tslint:disable-next-line:max-line-length
        this.currentEntityOptions = this.th2GenericFormComponent.formGroup.controls.filterForm.value.entity;
    }

    public buildFormConfig() {
        const options: Th2MatSelectOptionModel[] = [
            {
                disabled: false,
                label: Common.filterFormFields[Global.language].all,
                type: 'option',
                value: 'all'
            }
        ];

        const values = ['all'];

        this.formConfig = Global.language === 'es' ? FORM_CONFIG_ES : FORM_CONFIG_EN;
        for (const ent of Object.keys(EntityNameDictionary[Global.language])) {
            options.push({
                disabled: false,
                label: EntityNameDictionary[Global.language][ent],
                type: 'option',
                value: ent
            });

            values.push(ent);
        }

        // Entity选项进行赋值
        ((this.formConfig.elements[0] as Th2GenericFormGroupModel)
            .elements[3] as Th2MatSelectModel).options = options;
        ((this.formConfig.elements[0] as Th2GenericFormGroupModel)
            .elements[3] as Th2MatSelectModel).default = values;

        // SDG的All选项根据语言调整（All or Todos）
        (((this.formConfig.elements[0] as Th2GenericFormGroupModel)
            .elements[2] as Th2MatSelectModel).options[0] as Th2MatSelectOptionModel).label =
            Common.filterFormFields[Global.language].all;
    }

    public onSubmit(event) {
        const query = Object.assign({}, event.filterForm);
        query.dateFrom = this.normalizeTime.toBackend(query.dateFrom);
        query.dateTo = this.normalizeTime.toBackend(query.dateTo);
        this.onFormSubmit.emit(query);
    }

    public loadForm(filters: NewsFormQuery): void {
        this.th2GenericFormComponent.formGroup.controls.filterForm.patchValue(filters, {
            emitEvent: false
        });
    }

    public onChange(event): void {
        const entityCount = Object.keys(EntityNameDictionary[Global.language]).length;

        try {
            if (event.control === 'sdg') {
                const ESGIDList = [18, 19, 20];
                const ESGIDChanged = this.checkESGOptionsChanged(
                    this.currentSDGOptions,
                    event.value,
                    ESGIDList
                );

                if (this.checkAllOptionChanged(this.currentSDGOptions, event.value)) {
                    this.currentSDGOptions = this.getListAllChanged(
                        this.currentSDGOptions,
                        event.value
                    );
                } else if (ESGIDChanged > 0) {
                    ESGIDList.splice(ESGIDList.indexOf(ESGIDChanged), 1);

                    const ESGOthersIDList = [];
                    ESGIDList.forEach((id) => {
                        if (event.value.indexOf(id) >= 0) {
                            ESGOthersIDList.push(id);
                        }
                    });

                    this.currentSDGOptions = this.getListESGChanged(
                        this.currentSDGOptions,
                        event.value,
                        ESGIDChanged,
                        ESGOthersIDList
                    );
                } else {
                    this.currentSDGOptions = event.value;
                }

                // Check if 'All' or ESG options should be checked
                this.currentSDGOptions = this.checkContainESG(this.currentSDGOptions, 18);
                this.currentSDGOptions = this.checkContainESG(this.currentSDGOptions, 19);
                this.currentSDGOptions = this.checkContainESG(this.currentSDGOptions, 20);
                this.currentSDGOptions = this.checkContainAll(this.currentSDGOptions);

                // tslint:disable-next-line:max-line-length
                this.th2GenericFormComponent.formGroup.controls.filterForm.patchValue(
                    { sdg: this.currentSDGOptions },
                    { emitEvent: false }
                );
            }

            if (event.control === 'entity') {
                if (this.currentEntityOptions.length === entityCount + 1) {
                    if (event.value[0].toLowerCase() !== 'all') {
                        this.currentEntityOptions = [];
                    } else {
                        this.currentEntityOptions = event.value.slice(1);
                    }
                } else if (event.value.length > 0 && event.value[0].toLowerCase() === 'all') {
                    this.currentEntityOptions = ['all'];
                    Object.keys(EntityNameDictionary[Global.language]).forEach((k) =>
                        this.currentEntityOptions.push(k)
                    );
                } else {
                    this.currentEntityOptions = event.value;
                }

                // tslint:disable-next-line:max-line-length
                this.th2GenericFormComponent.formGroup.controls.filterForm.patchValue(
                    { entity: this.currentEntityOptions },
                    { emitEvent: false }
                );
            }
        } catch (e) {
            console.log(e);
        }
    }

    public checkAllOptionChanged(curr: number[], newArr: number[]): boolean {
        return (
            (curr.indexOf(0) < 0 && newArr.indexOf(0) >= 0) ||
            (curr.indexOf(0) >= 0 && newArr.indexOf(0) < 0)
        );
    }

    public checkESGOptionsChanged(curr: number[], newArr: number[], optionsID: number[]): number {
        let changedID = -1;

        optionsID.forEach((id) => {
            if (
                (curr.indexOf(id) < 0 && newArr.indexOf(id) >= 0) ||
                (curr.indexOf(id) >= 0 && newArr.indexOf(id) < 0)
            ) {
                changedID = id;
            }
        });

        return changedID;
    }

    public getListESGChanged(
        curr: number[],
        newArr: number[],
        esgIDChecked: number,
        esgIDOthers: number[]
    ): number[] {
        const sdgListChanged = ESGDictionary[esgIDChecked.toString()].SDG;
        const sdgListOthers = [];
        let results = [];

        curr.forEach((v) => results.push(v));

        esgIDOthers.forEach((id) => {
            const list = ESGDictionary[id.toString()].SDG;

            list.forEach((v) => {
                if (sdgListOthers.indexOf(v) < 0) {
                    sdgListOthers.push(v);
                }
            });
        });

        // 原来没有，现在有
        if (curr.indexOf(esgIDChecked) < 0 && newArr.indexOf(esgIDChecked) >= 0) {
            sdgListChanged.forEach((v: number) => {
                if (results.indexOf(v) < 0) {
                    results.push(v);
                }
            });
        }

        // 原来有，现在没有
        if (curr.indexOf(esgIDChecked) >= 0 && newArr.indexOf(esgIDChecked) < 0) {
            sdgListChanged.forEach((v: number) => {
                if (sdgListOthers.indexOf(v) < 0) {
                    results.splice(results.indexOf(v), 1);
                }
            });
        }

        results = results.sort((a, b) => a - b);

        return results;
    }

    public getListAllChanged(curr: number[], newArr: number[]): number[] {
        const sdgCount = Object.keys(SDGNameDictionary[Global.language]).length;
        return curr.indexOf(0) < 0 && newArr.indexOf(0) >= 0
            ? Array.from({ length: sdgCount + 4 }, (v, index) => index)
            : [];
    }

    public checkContainESG(arr: number[], sdgID: number): number[] {
        let results = [];
        arr.forEach((v) => results.push(v));

        const sdg = ESGDictionary[sdgID.toString()].SDG;
        let contain = true;

        sdg.forEach((v) => {
            if (results.indexOf(v) < 0) {
                contain = false;
            }
        });

        if (contain && results.indexOf(sdgID) < 0) {
            results.push(sdgID);
        } else if (!contain && results.indexOf(sdgID) >= 0) {
            results.splice(results.indexOf(sdgID), 1);
        }

        results = results.sort((a, b) => a - b);

        return results;
    }

    public checkContainAll(arr: number[]): number[] {
        let results = [];
        arr.forEach((v) => results.push(v));

        let contain = true;
        const sdgCount = Object.keys(SDGNameDictionary[Global.language]).length;

        Object.keys(SDGNameDictionary[Global.language]).forEach((v) => {
            if (results.indexOf(Number(v)) < 0) {
                contain = false;
            }
        });

        if (contain) {
            results = Array.from({ length: sdgCount + 4 }, (v, index) => index);
        } else if (results.indexOf(0) >= 0) {
            results.splice(results.indexOf(0), 1);
        }

        return results;
    }
}
