import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NewsService } from './news.service';
import { NewsFormQuery, NewsQuery } from './api/news-api.model';
import { NormalizeTime } from '../../services/NormalizeTime';
import { FilterFormComponent } from './components/filter-form/filter-form.component';
import { NewsTableComponent } from './components/news-table/news-table.component';
import * as XLSX from 'xlsx';
import { SDGNameDictionary } from '../model/sdg.enum';
import { EntityNameDictionary } from '../model/entity.enum';
import { Global } from '../../../global';
import { Common } from '../../utils/configuration/app/common';

@Component({
    selector: 'news-component',
    templateUrl: 'news.component.html'
})
export class NewsComponent implements OnInit, AfterViewInit {
    public news$ = this.newsService.news$;
    public sdgScores$ = this.newsService.sdgScores$;
    public titleNews: string = Common.titleNewsComponent[Global.language];

    @ViewChild(FilterFormComponent)
    private filterFormComponent: FilterFormComponent;

    @ViewChild(NewsTableComponent)
    private newsTableComponent: NewsTableComponent;

    constructor(
        private newsService: NewsService,
        private normalizeTime: NormalizeTime,
        private activatedRoute: ActivatedRoute
    ) {}

    // 若store的data未加载过，加载默认数据
    public ngOnInit() {
        if (!this.newsService.hasDataset()) {
            this.loadInitRequest();
        }

        this.activatedRoute.queryParams.subscribe((param) => {
            this.titleNews = Common.titleNewsComponent[Global.language];
            this.loadInitRequest();
        });
    }

    public ngAfterViewInit(): void {
        if (this.newsService.hasDataset()) {
            this.loadLastQuetionForm();
        }
    }

    // 提交filter表单的查询条件，根据查询条件获取数据并保存查询条件
    public onSubmit(newsQuery: NewsQuery) {
        this.newsService.loadDataset(newsQuery); // 加载数据
        this.newsService.saveLastFilter(newsQuery); // 保存最后的查询条件
    }

    // 页面初始数据加载（近30天的数据）
    private loadInitRequest(): void {
        const Day = 864e5;
        const nowDate = this.normalizeTime.toBackend(new Date(Date.now()));
        const yesterdayDate = this.normalizeTime.toBackend(new Date(Date.now() - Day * 30));
        const sdgCount = Object.keys(SDGNameDictionary[Global.language]).length;
        const ent = ['all'];
        Object.keys(EntityNameDictionary[Global.language]).forEach((k) => ent.push(k));

        const parameters = {
            dateFrom: yesterdayDate,
            dateTo: nowDate,
            sdg: Array.from({ length: sdgCount + 4 }, (item, index) => index),
            entity: ent,
            news_id: '',
            lang: 'en'
        };

        this.newsService.loadDataset(parameters);
        this.newsService.saveLastFilter(parameters);
    }

    private loadLastQuetionForm(): void {
        const lastFilter = this.newsService.getLastFilter();
        if (!lastFilter) {
            return;
        }

        const filter: NewsFormQuery = {
            ...lastFilter,
            dateTo: new Date(lastFilter.dateTo),
            dateFrom: new Date(lastFilter.dateFrom),
            entity: lastFilter.entity,
            sdg: lastFilter.sdg,
            news_id: lastFilter.news_id,
            lang: lastFilter.lang
        };

        this.filterFormComponent.loadForm(filter);
    }

    private exportToExcel(event) {
        const data = this.newsTableComponent.news.map((n) => {
            delete n.emotion;
            delete n.sentiment;
            delete n.newsId;
            delete n.lang;
            n['sdg'] = SDGNameDictionary[Global.language][n.sdgId];
            delete n.sdgId;

            return n;
        });

        const worksheet: XLSX.WorkSheet = XLSX.utils.json_to_sheet(data);
        const workbook: XLSX.WorkBook = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(workbook, worksheet, 'Sheet1');
        XLSX.writeFile(workbook, 'news.xlsx');
    }
}
